import * as React from "react";
import { Provider } from 'react-redux';
import { createStore } from 'redux';

import { HomeReducers } from  "../reducers/home.reducer";
import { HomeBanner } from "../components/HomeBanner";
import { HomeCatalogTitle } from "../components/HomeCatalogTitle";
import { BlogList } from "../components/BlogList";
import { DemoList } from "../components/DemoList";
import { QuickLinks } from "../components/QuickLinks";

import { NewBlogDatas } from "../servers/newBlogData";
import { DemoListDatas } from "../servers/demoListData";
import { LinksDatas } from "../servers/linksData";

let store = createStore(HomeReducers.testReducer, {
    isLock : false,
    inputText : ""
})


export default class Home extends React.Component<any, any>{

    componentDidMount = () =>{
        let demoCount = this.refs.DemoContent;
        $(demoCount).find("div.demo-item").each(function () {
            $(this).hoverdir({hoverDelay : 50});
        });
    }

    render(){

        let blogComponents : any,
            demoComponents : any,
            linksComponents : any;

        blogComponents = NewBlogDatas.NewBlogData.map(function (bloglist, index) {
            return <BlogList key={index} bloglist={bloglist} />;
        })

        demoComponents = DemoListDatas.DemoListData.map(function (demolist, index) {
            return <DemoList key={index} demolist={demolist} />;
        })

        linksComponents = LinksDatas.LinksDtaa.map(function (linkslist, index) {
            return <QuickLinks key={index} linkslist={linkslist} />;
        })


        return (
            <Provider store={store}>
                <div className="page">
                    <HomeBanner store={store} />
                    <div className="content ">
                        <div className="home-main">
                            <HomeCatalogTitle titletext="最新博文" titlebody="温故而知新，学而时习之，不亦说乎" />
                            <div className="blogContent">{blogComponents}</div>
                            <HomeCatalogTitle titletext="项目案例" titlebody="说的多不如做的多，熟能生巧，万变不离其宗" />
                            <div className="demoContent" ref="DemoContent">{demoComponents}</div>
                            <HomeCatalogTitle titletext="快速导航" titlebody="未来速度，为您直达核心内容" />
                            <div className="quickLinksContent">{linksComponents}</div>
                        </div>

                    </div>

                </div>
            </Provider>

        );
    }
}




